import { Collapse } from "antd";
import { CSelect } from "@components";

const CFixAssetStructure= ()=>{
    const { Panel } = Collapse
    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10}}>
            <Panel header="Structure" key="0">
               <CSelect
                  label="Main Fixed Asset"
                  name="main_fixed_asset"
                  id="main_fixed_asset"
                  autoFocus
               />
            </Panel>
         </Collapse>
      </>
    )
}
export default CFixAssetStructure;